export default {
      onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
      if(option.INC){
          this.code=option.INC;
            this.codeStatus=true;
      }
            
        },
    data() {
        return {
            checkUsername:"",
            checkPhone:"",
            checkPassword:"",
            checkRpassword:"",
            checkCode:"",
            username: "",
            phone: "",
            password: "",
            rpassword: "",
            code: "",
            codeStatus:false

        };
    },
    methods: {
     
        
        // 返回
        back(){
            uni.navigateBack({
                delta:1
            })
        },
        // 验证登录数据
        checkLogin() {
          if (!this.username.length >= 1 && this.username.length <=10) {
                this.checkUsername="请输入标准的昵称";
                return false;
            }
            if (!this.$u.test.mobile(this.phone)) {
                this.checkPhone="请输入正确的手机号";
                return false;
            }
            if (this.password.length < 4) {
                 this.checkPassword="请设置密码";

                return false;
            }
            if (this.rpassword !== this.password) {
                 this.checkRpassword="两次录入的密码不一致";
                return false;
            }
            if (this.code.length>10) {
                 this.checkCode="邀请码格式不正确";
                return false;
            }
            return true;
        },

        //注册
        register() {
            if(!this.checkLogin()) return;
            this.$u.post('/register',{
                username:this.username,
                phone: this.phone,
                password: this.password,
                rpassword: this.rpassword,
                code: this.code,
            }).then((res)=>{
                if(!res)return;
                if(res.errorCode===0){
                    uni.showToast({
                        title:"注册成功",
                    })
                   setTimeout(()=>{
                      uni.navigateTo({
                          url:"../login/login"
                      })
                   },1000)    
                }
            })
        }





},
onReady() {

}
}
